@import url(https://fonts.google.com/specimen/Alex+Brush);
.App {
  width: 100vw;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.7);
}
#tsparticles{
  width: 100%;
  height: 100%;
}
#tsparticles::before{
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  content: " ";
  background-image: url('./assets/laptop_1.jpg');
  background-size: cover;
  background-position:center;
  background-repeat: no-repeat;
  z-index: -1;
}
.pie {
  -webkit-tap-highlight-color: transparent;
  background: #000;
  border-radius: 50%;
  box-shadow: 0 0 4px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  height: 400px;
  left: -200px;
  position: absolute;
  top: -200px;
  width: 400px;
  transform: translateX(-200px) translateY(-200px);
  transition: transform 300ms;
}
.pie-color:hover {
  opacity: 0.85;
}
.pie-color:active {
  opacity: 0.7;
}
.pie1 {
  clip-path: polygon(200px 200px, 344px 450px, 0 450px);
  transition-delay: 30ms;
}
.pie2 {
  clip-path: polygon(200px 200px, 344px 450px, 450px 344px);
  transition-delay: 60ms;
}
.pie3 {
  clip-path: polygon(200px 200px, 450px 0, 450px 344px);
  transition-delay: 90ms;
}
.pie-color {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.pie-color1 {
  background: linear-gradient(to right, #4facfe, #00f2fe);
  clip-path: polygon(200px 200px, 344px 450px, 0 450px);
}
.pie-color2 {
  background: linear-gradient(to right, #667eea, #764ba2);
  clip-path: polygon(200px 200px, 344px 450px, 450px 344px);
}
.pie-color3 {
  background: linear-gradient(to right, #43e97b, #38f9d7);
  clip-path: polygon(200px 200px, 450px 0, 450px 344px);
}
.eye {
  left: 215px;
  position: absolute;
  top: 330px;
  width: 46px;
  font-size: 38px;
  color: white;
}
.user {
  left: 280px;
  position: absolute;
  top: 282px;
  width: 46px;
  font-size: 38px;
  color: white;
}
.home {
  left: 324px;
  position: absolute;
  top: 211px;
  width: 46px;
  font-size: 38px;
  color: white;
}
.menu {
  -webkit-tap-highlight-color: transparent;
  background: linear-gradient(to right, #f093fb, #f5576c);
  border-radius: 50%;
  box-shadow: 0 0 4px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  height: 200px;
  left: -100px;
  position: absolute;
  top: -100px;
  width: 200px;
}
.hamburger {
  cursor: pointer;
  height: 46px;
  left: 58%;
  position: relative;
  top: 58%;
  width: 46px;
}
.title {
  color: white;
  font-family: "Crimson Text", serif;
  font-size: 80px;
  line-height: 84px;
  margin-top: 60px;
  text-align: center;
}
.hamburger path {
  transition: transform 300ms;
}
.hamburger path:nth-child(1) {
  transform-origin: 25% 29%;
}
.hamburger path:nth-child(2) {
  transform-origin: 50% 50%;
}
.hamburger path:nth-child(3) {
  transform-origin: 75% 72%;
}
.hamburger path:nth-child(4) {
  transform-origin: 75% 29%;
}
.hamburger path:nth-child(5) {
  transform-origin: 25% 72%;
}
.active .pie {
  transform: translateX(0) translateY(0);
}
.active .hamburger path:nth-child(1) {
  transform: rotate(45deg);
}
.active .hamburger path:nth-child(2) {
  transform: scaleX(0);
}
.active .hamburger path:nth-child(3) {
  transform: rotate(45deg);
}
.active .hamburger path:nth-child(4) {
  transform: rotate(-45deg);
}
.active .hamburger path:nth-child(5) {
  transform: rotate(-45deg);
}


.text {
  position: absolute;
  left: 50%;
  margin-left: -170px;
  top: 50%;
  font-family: cursive;
}
.name {
  position: absolute;
  left: 50%;
  margin-left: -220px;
  margin-top: -140px;
  top: 50%;
}
.name>p{
  font-family: cursive;
  font-size: 60px;
  font-weight: bold;
  color: white;
}
p {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  font-family: cursive;
  color: white;
  font-size: 30px;
}

.word {
  position: absolute;
  width: 320px;
  left: 100px;
  opacity: 0;
}

.letter {
  display: inline-block;
  position: relative;
  float: left;
  transform: translateZ(25px);
  transform-origin: 50% 50% 25px;
}

.letter.out {
  transform: rotateX(90deg);
  transition: transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.letter.behind {
  transform: rotateX(-90deg);
}

.letter.in {
  transform: rotateX(0deg);
  transition: transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.wisteria {
  color: #667eea;
}

.belize {
  color: #2E7D32;
}

.pomegranate {
  color: #4facfe;
}

.green {
  color: #FFA000;
}

.midnight {
  color: #F50057;
}
